.header {
  position: absolute;
  z-index: 120;
  top: 0;
  left: 0;
  width: 100%;
  padding: 1rem 0;

  @media (min-width: 1200px) {
    padding: 3rem 0;
  }

  &__inner {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;

