      // @media (min-width: 1024px) {
      //   flex-direction: row;
      //   justify-content: space-between;
      //   background: transparent;
      // }
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;

    @media (min-width: 1200px) {
      margin-top: 0;
    }
  }
}